
"use server";

import { z } from 'zod';

const contactSchema = z.object({
  name: z.string().min(2, { message: "O nome deve ter pelo menos 2 caracteres." }),
  email: z.string().email({ message: "Por favor, insira um email válido." }),
  message: z.string().min(10, { message: "A mensagem deve ter pelo menos 10 caracteres." }),
});

export interface ContactFormState {
  message: string;
  errors?: {
    name?: string[];
    email?: string[];
    message?: string[];
  } | null;
  success?: boolean;
}

export async function submitContactForm(
  prevState: ContactFormState,
  formData: FormData
): Promise<ContactFormState> {
  const validatedFields = contactSchema.safeParse({
    name: formData.get('name'),
    email: formData.get('email'),
    message: formData.get('message'),
  });

  if (!validatedFields.success) {
    return {
      message: "Erro de validação. Por favor, corrija os campos destacados.",
      errors: validatedFields.error.flatten().fieldErrors,
      success: false,
    };
  }

  // Simulate API call or email sending
  console.log("Dados do formulário de contato:", validatedFields.data);
  await new Promise(resolve => setTimeout(resolve, 1000)); // Simulate network delay

  // Example: Randomly simulate an error
  // if (Math.random() > 0.7) {
  //   return {
  //     message: "Ocorreu um erro inesperado no servidor. Tente novamente.",
  //     success: false,
  //   };
  // }

  return {
    message: "Sua mensagem foi enviada com sucesso! Entrarei em contato em breve.",
    errors: null,
    success: true,
  };
}
