
"use client";

import { useActionState, useEffect, useRef } from 'react'; // Changed import
import { useFormStatus } from 'react-dom';
import { submitContactForm, type ContactFormState } from '@/app/actions';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { Send } from 'lucide-react';

function SubmitButton() {
  const { pending } = useFormStatus();
  return (
    <Button type="submit" disabled={pending} className="w-full sm:w-auto transition-transform hover:scale-105">
      {pending ? 'Enviando...' : 'Enviar Mensagem'}
      {!pending && <Send className="ml-2 h-4 w-4" />}
    </Button>
  );
}

export function ContactForm() {
  const initialState: ContactFormState = { message: "", errors: null, success: false };
  // Changed useFormState to useActionState
  const [state, formAction] = useActionState(submitContactForm, initialState);
  const { toast } = useToast();
  const formRef = useRef<HTMLFormElement>(null);

  useEffect(() => {
    if (state.message) {
      toast({
        title: state.success ? "Sucesso!" : "Atenção",
        description: state.message,
        variant: state.success ? "default" : "destructive",
      });
      if (state.success) {
        formRef.current?.reset();
      }
    }
  }, [state, toast]);

  return (
    <form ref={formRef} action={formAction} className="space-y-6">
      <div>
        <Label htmlFor="name" className="text-foreground/90">Nome</Label>
        <Input
          id="name"
          name="name"
          type="text"
          placeholder="Seu nome completo"
          required
          className="mt-1 bg-background/70"
        />
        {state.errors?.name && (
          <p className="mt-1 text-sm text-destructive">{state.errors.name.join(', ')}</p>
        )}
      </div>
      <div>
        <Label htmlFor="email" className="text-foreground/90">Email</Label>
        <Input
          id="email"
          name="email"
          type="email"
          placeholder="seuemail@exemplo.com"
          required
          className="mt-1 bg-background/70"
        />
        {state.errors?.email && (
          <p className="mt-1 text-sm text-destructive">{state.errors.email.join(', ')}</p>
        )}
      </div>
      <div>
        <Label htmlFor="message" className="text-foreground/90">Mensagem</Label>
        <Textarea
          id="message"
          name="message"
          placeholder="Como posso te ajudar?"
          rows={5}
          required
          className="mt-1 bg-background/70"
        />
        {state.errors?.message && (
          <p className="mt-1 text-sm text-destructive">{state.errors.message.join(', ')}</p>
        )}
      </div>
      <div>
        <SubmitButton />
      </div>
    </form>
  );
}

